<template>
  <div>
    <topMember v-on:member="getInfor"></topMember>
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">物流成本</span>
      </div>
      <div style="padding:40px;">
          <!-- <div class="cen">您当前的等级 <strong class="red">注册用户</strong></div> -->
          <el-row class="mar" >
            <el-col :span="9">
                <div class="cen"><strong>注册用户</strong></div>
                <table width="100%" class="mar biaoge" cellspacing="0" cellpadding="0">
                    <tr>
                      <th>地区</th>
                      <th>重量</th>
                      <th>价钱</th>
                    </tr>
                    <tr>
                      <td>普通地区</td>
                      <td>0.5kg内</td>
                      <td>{{priceForm.nearPrice1/100}}元/单</td>
                    </tr>
                    <tr>
                      <td>普通地区</td>
                      <td>0.5kg-1kg内</td>
                      <td>{{priceForm.nearPrice2/100}}元/单</td>
                    </tr>
                    <tr>
                      <td>普通地区</td>
                      <td>1kg以上续重</td>
                      <td>{{priceForm.nearPrice3/100}}元/kg</td>
                    </tr>
                    <tr>
                      <td>偏远地区</td>
                      <td>0.5kg内</td>
                      <td>{{priceForm.farPrice1/100}}元/单</td>
                    </tr>
                    <tr>
                      <td>偏远地区</td>
                      <td>0.5kg-1kg内</td>
                      <td>{{priceForm.farPrice2/100}}元/单</td>
                    </tr>
                    <tr>
                      <td>偏远地区</td>
                      <td>1kg以上续重</td>
                      <td>{{priceForm.farPrice3/100}}元/kg</td>
                    </tr>
                </table>
            </el-col>
            <el-col :span="6">
                <div class="cen yqmain" >
                    <div>总计邀请 3 人免费</div>
                    <div class="mar red" style="margin-top: 15px">升级为 VIP</div>
                    <div class="mar">已邀请 <span class="red">{{member.invitedCount}}</span> 人</div>
                    <el-button class="mar" @click="copyUrl" type="primary">复制邀请链接</el-button>
                </div>
            </el-col>
            <el-col :span="9">
               <div class="cen"><strong>VIP用户</strong></div>
              <table width="100%" class="mar biaoge" cellspacing="0" cellpadding="0">
                <tr>
                  <th>地区</th>
                  <th>重量</th>
                  <th>价钱</th>
                </tr>
                <tr>
                  <td>普通地区</td>
                  <td>0.5kg内</td>
                  <td>{{vipPriceForm.nearPrice1/100}}元/单</td>
                </tr>
                <tr>
                  <td>普通地区</td>
                  <td>0.5kg-1kg内</td>
                  <td>{{vipPriceForm.nearPrice2/100}}元/单</td>
                </tr>
                <tr>
                  <td>普通地区</td>
                  <td>1kg以上续重</td>
                  <td>{{vipPriceForm.nearPrice3/100}}元/kg</td>
                </tr>
                <tr>
                  <td>偏远地区</td>
                  <td>0.5kg内</td>
                  <td>{{vipPriceForm.farPrice1/100}}元/单</td>
                </tr>
                <tr>
                  <td>偏远地区</td>
                  <td>0.5kg-1kg内</td>
                  <td>{{vipPriceForm.farPrice2/100}}元/单</td>
                </tr>
                <tr>
                  <td>偏远地区</td>
                  <td>1kg以上续重</td>
                  <td>{{vipPriceForm.farPrice3/100}}元/kg</td>
                </tr>
              </table>
            </el-col>
          </el-row>
      </div>
    </div>
    <foot></foot>


  </div>
</template>

<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      priceForm:'',
      vipPriceForm:'',
      member:'',
    };
  },
  mounted(){
    // this.getPrice();
    this.getVipPrice();
  },
  methods: {
    getInfor(data) {
      this.member = data;
    },
    copyUrl(){
      let rl = window.location.href;
      let index= rl.indexOf('.com')+4;
        var input = document.createElement('input');
        input.setAttribute('readonly', 'readonly'); // 防止手机上弹出软键盘
        input.setAttribute('value', rl.substring(0,index)+'/#/?inviteCode='+this.member.inviteCode);
        document.body.appendChild(input);
        // input.setSelectionRange(0, 9999);
        input.select();
        var res = document.execCommand('copy');
        document.body.removeChild(input);
        this.$message({
        type: "info",
        message: `已复制到粘贴板`
      });
    },
    getVipPrice(){
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/judge",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.priceForm=response.data.data[0];
            that.vipPriceForm=response.data.data[1];
          }
        });
    },
    getPrice() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/my",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.priceForm=response.data.data;
          }
        });
    },
  }
};
</script>

<style scoped>
.biaoge {
  border:1px solid #ec3440;
}
.biaoge th {
  background:#ec3440;
  color:#fff;
  padding:5px;
}
.biaoge td {
  padding:10px;
  text-align: center;
  border-bottom:1px solid #eaeaea;
}
.yqmain {
  margin:50px 20px;
  font-weight:bold;
  background:url(../../static/jt.png) no-repeat center 35px;
  background-size:100% auto;
}
.yqmain .mar {

}
</style>




